'use client';

import { useState, useEffect } from 'react';
import {
  Map,
  APIProvider,
  AdvancedMarker,
  Pin,
  MapMouseEvent, // Import the correct event type
} from '@vis.gl/react-google-maps';

// Define types for the props
interface Location {
  lat: number;
  lng: number;
}

interface PinData {
  id: string;
  name: string;
  location: {
    latitude: number;
    longitude: number;
  };
  type?: string; // Add optional type property to be safe
}

interface DestinationData extends PinData {
  reward: number;
}

interface MapComponentProps {
  onMapClick: (location: Location) => void;
  pins: PinData[];
  destinations: DestinationData[];
}

// Renamed from App to MapComponent for clarity
export default function MapComponent({ onMapClick, pins, destinations }: MapComponentProps) {
  const [currentPosition, setCurrentPosition] = useState<Location | null>(null);

  useEffect(() => {
    // Get user's current location
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setCurrentPosition({ lat: latitude, lng: longitude });
        },
        (error) => {
          console.error('Error getting user location:', error);
          // Default to Denver if location is denied or fails
          setCurrentPosition({ lat: 39.7392, lng: -104.9903 });
        }
      );
    } else {
      // Default to Denver if geolocation is not supported
      setCurrentPosition({ lat: 39.7392, lng: -104.9903 });
    }
  }, []); // Empty dependency array ensures this runs only once on mount

  const handleMapClick = (event: MapMouseEvent) => { // Use the imported MapMouseEvent
    if (event.detail.latLng) { // Check if latLng is not null
      const lat = event.detail.latLng.lat;
      const lng = event.detail.latLng.lng;
      if (onMapClick) {
        onMapClick({ lat, lng });
      }
    }
  };

  if (!currentPosition) {
    return <div>Loading Map...</div>; // Or a spinner
  }

  return (
    <APIProvider apiKey={process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY!}>
      <div style={{ height: '100vh', width: '100%' }}>
        <Map
          zoom={14} // More appropriate zoom for a local view
          center={currentPosition}
          mapId={process.env.NEXT_PUBLIC_MAP_ID!}
          onClick={handleMapClick}
        >
          {pins &&
            pins.map((pin) => (
              <AdvancedMarker
                key={pin.id}
                position={{
                  lat: pin.location.latitude,
                  lng: pin.location.longitude,
                }}
                title={pin.name}
              ></AdvancedMarker>
            ))}
          {destinations &&
            destinations.map((destination) => (
              <AdvancedMarker
                key={destination.id}
                position={{
                  lat: destination.location.latitude,
                  lng: destination.location.longitude,
                }}
                title={destination.name}
              >
                <Pin
                  background={destination.type === 'user-destination' ? '#FFC107' : '#FE7569'}
                  borderColor={destination.type === 'user-destination' ? '#FFC107' : '#FE7569'}
                  glyphColor={'#000000'}
                ></Pin>
              </AdvancedMarker>
            ))}
        </Map>
      </div>
    </APIProvider>
  );
}
